.barra {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 3rem;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .4s ease-in-out, opacity .4s linear, height .5s cubic-bezier(1, .8, .5, 0.1);;

    @include tablet {
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
    }

    a {
        display: block;
        text-align: center;
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;

        &:hover {
            color: $verde;
        }

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}

.barra-menu {
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
        cursor: pointer;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: 18rem;
}

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }
}
.dark-mode {
    width: 4rem;
    filter: invert(100%);
    margin-top: 2rem;
    margin-bottom: 2rem;
    cursor: pointer;

    @include tablet {
        margin-top: 0;
    }
}