.no-webp .imagen-mostrar {
    background-image: url(../../../build/img/encuentra.jpg);
}
.webp .imagen-mostrar {
    background-image: url(../../../build/img/encuentra.webp);
}

.imagen-mostrar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10rem 0;

    h2 {
        font-size: 4rem;
        color: $blanco;
        font-weight: $black;
    }
    p {
        color: $blanco;
        font-size: 1.8rem;
    }
} 