@include tablet {
    .contenedor-anuncios {
        @include grid(3, 2rem);
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid $gris;

    .contenido-anuncio {
        padding: 2rem;
    }
    h3, p {
        margin: 0;
    }
    
}
.precio {
    color: $verde;
    font-size: 3rem;
    font-weight: $bold;
} 

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    justify-content: space-around;
    max-width: 40rem;

    li {
        display: flex;
        gap: 1rem;

        img {
            flex: 0 0 3rem;
        }
        p {
            font-weight: $black;
        }
    }
}